import React, { FormEvent, useState, useEffect } from "react"
import MyInput from "../common/MyInput"
import Axios from "axios"
import { Redirect } from "react-router-dom"

const Login = () => {

  const [username, setUsername] = useState()
  const [password, setPassword] = useState()
  const [loggedIn, setLoggedIn] = useState(false)
  const [error, setError] = useState('')

  useEffect(() => {
    if(localStorage.getItem("token")) setLoggedIn(true)
  })


  async function login(e: FormEvent<HTMLFormElement>) {
    e.preventDefault()
    if (username.trim() === '' && password.trim() === '') {
      setError("Enter all details")
      return
    }

    const data = {
      username,
      password
    }
    try {
      const res = await Axios.post("/api/login", data)
      if (res.status === 200) {
        const token = res.data.token
        localStorage.setItem("token", token)
        setLoggedIn(true)
      } else {
        setError(res.data.message)
      }

    } catch (error) {
      setError(error.message)
    }


  }

  if (loggedIn) {
    return <Redirect to="/home" />
  }

  return (
    <div className="container">
      <div className="jumbotron justify-content-md-center">
        <h1 className="text-center">Zattire</h1>
        <form onSubmit={login}>
          <MyInput label="Username" onChange={(e: React.ChangeEvent<HTMLInputElement>) => setUsername(e.currentTarget.value)} />
          <MyInput label="Password" type="password" onChange={(e: React.ChangeEvent<HTMLInputElement>) => setPassword(e.currentTarget.value)} />
          <button type="submit" className="btn btn-success">Submit</button>
          <p className="text-danger">{error}</p>
        </form>
      </div>
    </div>
  );
}

export default Login