import React, { useState } from "react"
import Axios from "axios"
import { AdminI } from "./admin-interfaces"
import MyInput from "../../common/MyInput"
import MyNavBar from "../../common/MyNavBar"

const CreateAdmin = () => {
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [role, setRole] = useState('')
    const [error, setError] = useState('')

    async function postData() {
        if (username.trim() !== '' && password.trim() !== '' && role.trim() !== '') {
            setError("Enter all details")
            return
        }
        const data: AdminI = {
            username,
            password,
            role
        }
        const res = await Axios.post("/api/login/create", data)
        if (res.status == 200) {

        } else {
            const error: string = res.data.message
            if (!error) {
                setError('Error')
                return
            }
            setError(error)
        }
    }
    return (
        <React.Fragment>
            <MyNavBar />
            <div className="container">
                <div className="jumbotron">
                    <form>
                        <h1>Add Admin</h1>
                        <MyInput onChange={(e: React.ChangeEvent<HTMLInputElement>) => setUsername(e.target.value)} label="Username" />
                        <MyInput onChange={(e: React.ChangeEvent<HTMLInputElement>) => setPassword(e.target.value)} label="Password" />
                    </form>
                </div>
            </div>
        </React.Fragment>
    )
}

export default CreateAdmin